
#app {
    width: 100%;
    height: 100%;
    overflow: auto;
}

#download-page .el-input-number span {
  height: 29px;
}
#download-page .el-input-number span i {
  line-height: 30px;
}
#download-page .el-breadcrumb {
  line-height: 3rem;
  background-color: #e9ecef;
}

.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}











































































































































































































































































































































































#p-nav .nav-icon-right-sm {
  padding: 0 9px;
  border-bottom: 0;
}
#p-nav svg.outline path {
  color: red;
}
#p-nav #logo {
  font-size: 20pt;
  border-bottom: 0;
  color: white;
}
#p-nav #rpc-link {
